<style lang="scss">
  .footer {
    width: 100%;
    background: #000;
    position: relative;

    &-blocks {
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: flex-start;
      padding: 55px 0;

      @media only screen and (max-width: 768px) {
        flex-direction: column;
        padding: 30px 0 0;
      }
    }

    &-block {
      width: 33.3%;
      max-width: 250px;
      display: flex;
      flex-direction: column;
      @media only screen and (max-width: 768px) {
        width: 100%;
        max-width: 100%;
        margin-bottom: 30px;
      }

      &:first-child {
        @media only screen and (max-width: 768px) {
          align-items: center;
        }
      }

      &:last-child {
        max-width: 234px;
        @media only screen and (max-width: 768px) {
          max-width: 100%;
        }
      }
    }

    &-logo {
      display: flex;
      justify-content: flex-start;
      margin-bottom: 30px;

      @media only screen and (max-width: 768px) {
        margin-bottom: 15px;
      }

      img {
        transition: opacity .3s ease;

        &:hover {
          opacity: .7;
        }
      }
    }

    &-text {
      font-weight: 400;
      font-size: 14px;
      line-height: (20/14);
      color: #FFFFFF;
      @media only screen and (max-width: 768px) {
        width: 100%;
        font-weight: 500;
        text-align: left;
      }
    }

    &-link {
      &-title {
        text-transform: uppercase;
        font-weight: 700;
        font-size: 14px;
        line-height: (20/14);
        color: #E0BC6C;
        margin-bottom: 10px;
      }

      font-weight: 400;
      font-size: 14px;
      line-height: (20/14);
      color: #FFFFFF;
      transition: .3s ease;
      margin-bottom: 5px;

      &:hover {
        color: #e0bc6c;
      }
    }

    &-social {
      margin-top: 15px;
      width: 100%;
      display: flex;

      &-link {
        width: 44px;
        height: 44px;
        display: flex;
        justify-content: center;
        align-items: center;
        border: 1px solid rgba(255, 255, 255, 0.3);
        transition: .3s ease;
        margin-right: 16px;
        border-radius: 50%;

        &:last-child {
          margin-right: 0;
        }

        svg path {
          transition: .3s ease;
        }

        &:hover {
          background: #fff;

          svg path {
            fill: #000;
          }
        }
      }
    }

    &-rights {
      width: 100%;
      padding: 25px 15px;
      display: flex;
      justify-content: center;
      align-items: center;
      position: relative;
      z-index: 5;
      font-weight: 400;
      font-size: 14px;
      line-height: (20/14);
      color: #FFFFFF;
      background: #191919;
      text-align: center;
    }

  }

</style>



