<template>
  <section id="hero" class="section section-hero hero">

    <div class="hero__coin hero__coin_1" data-speed="0.3">
      <img src="../assets/img/pages/home/hero/coins/coin1.png" alt="">
    </div>
    <div class="hero__coin hero__coin_2" data-speed="0.4">
      <img src="../assets/img/pages/home/hero/coins/coin2.png" alt="">
    </div>
    <div class="hero__coin hero__coin_3" data-speed="0.5">
      <img src="../assets/img/pages/home/hero/coins/coin3.png" alt="">
    </div>
    <div class="hero__coin hero__coin_4" data-speed="0.3">
      <img src="../assets/img/pages/home/hero/coins/coin4.png" alt="">
    </div>
    <div class="hero__coin hero__coin_5" data-speed="0.45">
      <img src="../assets/img/pages/home/hero/coins/coin5.png" alt="">
    </div>
    <div class="hero__coin hero__coin_6" data-speed="0.45">
      <img src="../assets/img/pages/home/hero/coins/coin6.png" alt="">
    </div>

    <div class="landing-container">
      <div class="hero-content">
        <h1 class="hero-title">
          {{$t('hero.title')}} <span> {{$t('hero.title_from')}} </span> 88<span>%</span>
        </h1>
        <h4 class="hero-subtitle">{{$t('hero.subtitle')}}</h4>
        <div class="hero-btns">
          <a :href="`https://shop.karat.jewelry`"
             target="_blank"
             class="button button-sm hero-btn-shop">
            <div class="button-icon">
              <img src="../assets/img/layout/buttons/shop.svg" alt="">
            </div>
            <span class="button-text">{{$t('hero.button_shop')}}</span>
          </a>
          <!-- <a href="https://backoffice.cashback-global.io/sign_up" class="button button-sm button-dark">
            <div class="button-icon">
              <img src="../assets/img/layout/buttons/plus.svg" alt="">
            </div>
            <span class="button-text">{{$t('hero.button_registration')}}</span>
          </a> -->
        </div>
      </div>
    </div>
    <div class="hero-arrow-slide" v-scroll-to="'#about_us'"></div>
  </section>
</template>

<script>
  export default {
    name: 'Hero',
    data() {
      return {}
    },
    mounted() {
      window.document.addEventListener('DOMContentLoaded', function () {
        const coins = document.querySelectorAll('.hero__coin');



        coins.forEach((element, index) => {
          let topPosition = +getComputedStyle(element).top.slice(0, -2);

          window.addEventListener('scroll', function () {
            let needSectionPosition = document.querySelector('.section-hero').getBoundingClientRect().top

            element.style.top = `${topPosition - needSectionPosition * +element.dataset.speed}px`
          })
        })


      })
    }
  }

</script>

<style lang="scss" scoped>
  .hero {
    min-height: 100vh;
    width: 100%;
    background: #000;
    background-image: url("../assets/img/pages/home/hero/main-background.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: bottom;
    position: relative;

    @media only screen and (max-width: 600px) {
      background-image: url("../assets/img/pages/home/hero/main-background-mobile.jpg");
    }

    @media only screen and (min-width: 600px) and (max-height:700px) {
      min-height: 700px;
    }

    &:after {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 270px;
      background-image: url("../assets/img/pages/home/hero/main-after-mobile.png");
      background-size: cover;
      background-repeat: no-repeat;
      background-position: bottom;

      @media only screen and (min-width: 600px) {
        content: none;
      }
    }

    .landing-container {
      padding: 170px 30px 30px;
      position: relative;

      @media only screen and (max-width: 600px) {
        padding: 110px 30px 120px;
      }

    }

    &-content {
      width: 100%;
      position: relative;
      z-index: 15;
    }

    &-title {
      font-family: "Playfair Display", sans-serif;
      font-weight: 700;
      font-size: 70px;
      line-height: (93/70);
      color: #FFFFFF;
      margin-bottom: 30px;
      text-align: center;

      @media only screen and (max-width: 990px) {
        font-size: 55px;
      }

      @media only screen and (max-width: 600px) {
        font-size: 40px;
        margin-bottom: 30px;
      }

      span {
        font-size: 35px;

        @media only screen and (max-width: 990px) {
          font-size: 32px;
        }

        @media only screen and (max-width: 600px) {
          font-size: 28px;
        }
      }
    }

    &-subtitle {
      font-weight: 700;
      font-size: 18px;
      line-height: 1.2;
      text-align: center;
      color: #E0BC6C;
      margin-bottom: 90px;

      @media only screen and (max-width: 600px) {
        margin-bottom: 30px;
      }

      @media only screen and (min-width: 600px) and (max-height:700px) {
        margin-bottom: 45px;
      }
    }

    &-btns {
      display: flex;
      justify-content: center;
      align-items: center;

      @media only screen and (max-width: 600px) {
        flex-direction: column;
      }
    }

    &-btn {
      &-shop {
        margin-right: 20px;
        max-width: 255px;

        @media (max-width: 600px) {
          margin-right: 0;
          margin-bottom: 20px;
        }
      }
    }

    &-arrow-slide {
      position: absolute;
      bottom: 50px;
      left: 50%;
      transform: translateX(-50%);
      width: 100px;
      height: 100px;
      transition: opacity .3s ease;
      cursor: pointer;
      z-index: 5;

      &:hover {
        opacity: .7;
      }

      &:before {
        content: "";
        position: absolute;
        top: 30px;
        left: 40px;

        width: 20px;
        height: 70px;
        background-image: url("../assets/img/pages/home/hero/arrow-slide.svg");
        background-repeat: no-repeat;
        background-size: contain;
        background-position: center;
        transform-origin: bottom;
        animation-name: hero-arrow;
        animation-timing-function: ease;
        align-self: flex-end;
        animation-duration: 2s;
        animation-iteration-count: infinite;
      }

      @keyframes hero-arrow {
        0% {
          transform: translateY(0);
        }

        50% {
          transform: translateY(-20px);
        }

        100% {
          transform: translateY(0);
        }
      }
    }

    &__coin {
      position: absolute;
      z-index: 1;
      transition: .05s;

      img {
        width: 100%;
      }

      &_1 {
        left: -80px;
        top: 100px;
      }

      &_2 {
        left: 160px;
        top: 220px;
      }

      &_3 {
        top: 350px;
        left: 0;
      }

      &_4 {
        top: 230px;
        right: 150px;
      }

      &_5 {
        top: 420px;
        right: 60px;
      }

      &_6 {
        top: 50px;
        right: -120px;
      }

      @media (max-width:1500px) {
        &_2 {
          left: 100px;
        }

        &_4 {
          right: 70px;
        }
      }

      @media (max-width: 1300px) {
        &_2 {
          left: 50px;
        }

        &_4 {
          right: 20px;
        }
      }

      @media (max-width: 1200px) {
        display: none;
      }
    }


  }

</style>



