export default {
  install(Vue) {
    Vue.prototype.$title = function (titleKey) {
      const appName = ((localStorage.locale === 'ru') ? '' : 'Karat')
      if (titleKey.length) {
        return `${appName} | ${titleKey}`
      } else {
        return `${appName}`
      }
    }
  }
}



