<template>
  <section id="faq" class="section section-faq">
    <div class="section-container">
      <div class="faq">
        <h2 class="title faq-title">
          {{ $t('faq.title') }}
        </h2>
        <div class="faq-questions">
          <!-- indexes starts with 1 -->
          <dl class="accordion faq-question" v-for="index in 8" v-bind:key="index">
            <dt @click="toggleItem(index)"
                :class="{'is-active' : activeAnswerIndex === index}"
                class="faq-question-header">
              <p class="faq-question-text">
                {{ (index) + '.' }} {{ $t('faq.question' + '_' + (index)) }}
              </p>

              <button class="faq-question-button" />
            </dt>
            
            <dd :class="{'is-active' : activeAnswerIndex === index}"
                class="faq-answer">
              {{ $t('faq.answer' + '_' + (index)) }}
            </dd>
          </dl>
        </div>
      </div>
    </div>
  </section>
</template>

<script>

export default {
  name: 'FAQ',
  data() {
    return {
      activeAnswerIndex: null,
    }
  },
  methods: {
    toggleItem: function (index) {
      this.activeAnswerIndex = this.activeAnswerIndex === index
        ? null
        : index;
    }
  }

}
</script>

<style lang="scss" scoped>

.section {
  &-faq {
    background-image: url("../assets/img/pages/home/faq/background-faq.jpg");
    background-size: cover;
    padding: 0;
    position: relative;
    min-height: 775px;

    @media (max-width: 900px) {
      min-height: auto;
    }

    &:before {
      content: "";
      position: absolute;
      width: 480px;
      height: 550px;
      left: 30px;
      bottom: 0;
      background-image: url("../assets/img/pages/home/faq/faq-image.png");
      background-repeat: no-repeat;
      background-position: top;

      @media (max-width: 1220px) {
        left: -100px;
      }

      @media (max-width: 900px) {
        content: unset;
      }
    }
  }

  &-container {
    width: 100%;
    max-width: 1040px;
    margin: auto;
    padding: 130px 20px 135px;
    position: relative;

    @media (max-width: 900px) {
      padding: 30px 20px;
    }
  }
}

.faq {
  display: flex;

  @media (max-width: 900px) {
    flex-direction: column;
    align-items: center;
  }

  &-title {
    width: 320px;
    margin-right: 78px;
    color: #e0bc6c;

    @media (max-width: 900px) {
      width: 100%;
      margin-right: 0;
      margin-bottom: 30px;
      text-align: center;
    }

    @media (max-width: 600px) {
      text-align: left;
    }
  }

  &-questions {
    width: calc(100% - 320px - 78px);

    @media (max-width: 900px) {
      width: 100%;
    }
  }

  &-question {
    margin-bottom: 20px;

    &:last-child {
      margin-bottom: 0;
    }

    &-header {
      display: flex;
      justify-content: space-between;
      align-items: center;

      &.is-active {
        .faq-answer {
          display: block;
        }

        .faq-question-button {
          &:before, &:after {
            background-color: #fff;
          }

          &:after {
            transform: translate(-50%, -50%) rotate(90deg);
          }
        }
      }
    }

    &-text {
      margin-right: 20px;
      font-size: 14px;
      line-height: (20 / 14);
      font-weight: 700;
      text-transform: uppercase;
      color: #e0bc6c;
    }

    &-button {
      width: 48px;
      height: 48px;
      min-width: 48px;
      border: 1px solid rgba(255, 255, 255, 0.3);
      background-color: transparent;
      border-radius: 50%;
      position: relative;

      &:hover {
        &:before,
        &:after {
          background-color: #fff;
        }
      }

      &:before,
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%) rotate(0);
        background-color: #e0bc6c;
        transition: background-color .3s ease;
      }

      &:before {
        width: 10px;
        height: 2px;
      }

      &:after {
        width: 2px;
        height: 10px;
      }
    }
  }

  &-answer {
    margin-top: 0;
    font-weight: 500;
    color: #fff;
    font-size: 16px;
    line-height: (24 / 16);
    width: calc(100% - 68px);

    &.is-active {
      margin-top: 10px;
    }

    @media (max-width: 600px) {
      font-size: 14px;
    }
  }
}


.accordion {
  dd {
    display: none;

    &.is-active {
      display: block;

    }
  }
}

</style>



