<template>
  <header id="header" class="header" v-bind:class="{ 'is-open': isOpenMenu }">
    <div class="header-mobile">
      <div class="header-mobile-btn header-mobile-burger" v-on:click="isOpenMenu = !isOpenMenu">
        <svg
          class="header-mobile-burger-open"
          width="24"
          height="24"
          viewBox="0 0 24 24"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path d="M3 12H21" stroke="#E0BC6C" stroke-width="1.5" stroke-linecap="square"/>
          <path d="M3 6H21" stroke="#E0BC6C" stroke-width="1.5" stroke-linecap="square"/>
          <path d="M3 18H21" stroke="#E0BC6C" stroke-width="1.5" stroke-linecap="square"/>
        </svg>
        <svg
          class="header-mobile-burger-close"
          width="24"
          height="24"
          viewBox="0 0 24 24"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path d="M18 6L6 18" stroke="#E0BC6C" stroke-width="1.5" stroke-linecap="square"/>
          <path d="M6 6L18 18" stroke="#E0BC6C" stroke-width="1.5" stroke-linecap="square"/>
        </svg>
      </div>

      <a href="/" class="header-logotype" v-on:click="isOpenMenu = false">
        <img src="../assets/img/layout/header/logotype-mobile.svg"
             v-if="$i18n.locale === 'ru'" alt="Karat"/>
        <img src="../assets/img/layout/header/logotype-mobile-en.svg"
             v-else alt="Karat"/>
      </a>
      <a href="https://backoffice.cashback-global.io/personal-office"
        class="header-mobile-btn">
        <svg
          width="24"
          height="24"
          viewBox="0 0 24 24"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path
            d="M16 21V19C16 17.9391 15.5786 16.9217 14.8284 16.1716C14.0783 15.4214 13.0609 15 12 15H5C3.93913 15 2.92172 15.4214 2.17157 16.1716C1.42143 16.9217 1 17.9391 1 19V21"
            stroke="#E0BC6C"
            stroke-width="1.5"
            stroke-linecap="square"
            stroke-linejoin="round"
          />
          <path
            d="M8.5 11C10.7091 11 12.5 9.20914 12.5 7C12.5 4.79086 10.7091 3 8.5 3C6.29086 3 4.5 4.79086 4.5 7C4.5 9.20914 6.29086 11 8.5 11Z"
            stroke="#E0BC6C"
            stroke-width="1.5"
            stroke-linecap="square"
            stroke-linejoin="round"
          />
          <path
            d="M20 8V14"
            stroke="#E0BC6C"
            stroke-width="1.5"
            stroke-linecap="square"
            stroke-linejoin="round"
          />
          <path
            d="M23 11H17"
            stroke="#E0BC6C"
            stroke-width="1.5"
            stroke-linecap="square"
            stroke-linejoin="round"
          />
        </svg>
      </a>
    </div>
    <div class="header-content" @scroll="onScroll">
      <div class="header-links-block">
        <router-link to="/#hero" v-scroll-to="'#hero'" class="header-link" v-on:click="isOpenMenu = false" >
          <span :class="{ black_text: black_color }">
            {{$t('header.menu_main')}}
          </span>
        </router-link>
        <a href="/#about_us" class="header-link"
           v-on:click="isOpenMenu = false">
          <span :class="{ black_text: black_color }">
            {{$t('header.menu_about')}}
          </span>
        </a>
        <router-link to="/news" class="header-link hidden"
                     v-on:click="isOpenMenu = false"
        >
          <span :class="{ black_text: black_color }">
            {{$t('header.menu_news')}}
          </span>
        </router-link>
        <a :href="`https://shop.karat.jewelry`"
           target="_blank"
           class="header-link">
          <span :class="{ black_text: black_color }">
            {{$t('header.menu_shop')}}
          </span>
        </a>
      </div>
      <a href="/" class="header-logotype" v-on:click="isOpenMenu = false">
        <!--<img src="../assets/img/layout/header/logotype-desktop.svg" alt />-->

        <img src="../assets/img/layout/header/logotype-mobile.svg"
             v-if="$i18n.locale === 'ru'" alt="Karat"/>
        <img src="../assets/img/layout/header/logotype-mobile-en.svg"
             v-else alt="Karat"/>
      </a>
      <div class="header-links-block">

        <LocaleSwitcher/>

        <router-link to="#footer" v-scroll-to="'#footer'" class="header-link" v-on:click="isOpenMenu = false" >
          <span :class="{ black_text: black_color }">
            {{$t('header.menu_contact')}}
          </span>
        </router-link>

        <a :href="cashbackGlobalRoutes.landing"
          class="header-link sign-up"
        >
          <span>
            {{$t('header.activate_cashback')}}
          </span>
          <svg
            width="9"
            height="12"
            viewBox="0 0 9 12"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path d="M9 6L0.75 0.803848V11.1962L9 6Z" fill="white"/>
          </svg>
        </a>
      </div>
    </div>
  </header>
</template>

<script>
import LocaleSwitcher from "./LocaleSwitcher";
import cashbackGlobalRoutes from '@/config/cashbackGlobalRoutes.js';

  export default {
    name: "Header",
    components: {
      LocaleSwitcher,
    },
    data() {
      return {
        isOpenMenu: false,
        cashbackGlobalRoutes,
        //dark_theme: false,
      };
    },
    mounted() {
      if (
        this.$route.matched[0].path === "/news" ||
        this.$route.matched[0].path === "/news/:id"
      ) {
        //console.log("this.$route.matched[0].path", this.$route);
        //this.dark_theme = true;
      }
    },
    computed: {
      black_color: function () {
        if (
          (this.$route.matched[0].path === "/news" ||
            this.$route.matched[0].path === "/news/:id") &
          (this.isOpenMenu === false)
        ) {
          return true;
        } else {
          return false;
        }
      },

    },
    methods: {
      onScroll(event) {
        //console.log("event", event.target.defaultView.scrollY);
      },
    },
    created() {
      window.addEventListener("scroll", this.onScroll);
    },
    destroyed() {
      window.removeEventListener("scroll", this.onScroll);
    },
  };
</script>

<style lang="scss" scoped>
  .black_text {
    //color: #000000;
    color: #ffffff;
  }

  .header {
    position: fixed;
    //position: absolute;
    //top: 0;
    //left: 0;
    width: 100%;
    background: #000;
    z-index: 50;
    border-bottom: 1px solid #2c2c2c;
    //.header-mobile {
    //position: absolute;
    //top: 0;
    //left: 0;
    //position: fixed;
    // width: 100%;
    //background:red;
    //}

    &.is-open {
      .header-mobile {
        //position: absolute;
        //top: 0;
        //left: 0;
        position: fixed;
        width: 100%;
      }

      .header-content {
        position: fixed;
        opacity: 1;
        z-index: 55;
        visibility: visible;
      }

      .header-mobile-burger-open {
        opacity: 0;
        visibility: hidden;
        //color: #E0BC6C;
      }

      .header-mobile-burger-close {
        opacity: 1;
        visibility: visible;
        //color: #E0BC6C;
      }
    }

    &-content {
      opacity: 1;
      z-index: 55;
      visibility: visible;
      height: 80px;
      width: 100%;
      max-width: 1440px;
      margin: 0 auto;
      display: flex;
      justify-content: space-between;
      align-items: center;
      position: relative;
      padding: 0 30px;

      .header-logotype {
        width: 400px;
        height: 80px;
        display: flex;
        align-items: center;
        justify-content: center;

        &:before {
          content: "";
          position: absolute;
          bottom: 0;
          left: 0;
          width: 100%;
          height: 100%;
          background: linear-gradient(
              180deg,
              rgba(255, 255, 255, 0.105) 0%,
              rgba(255, 255, 255, 0) 100%
          );
          @media only screen and (max-width: 1200px) {
            background: none;
            backdrop-filter: none;
          }
        }

        img {
          position: relative;
          width: 156px;
          height: 27px;
        }

        @media only screen and (max-width: 1260px) {
          width: 300px;
        }
        @media only screen and (max-width: 1200px) {
          width: auto;
        }
      }

      @media only screen and (max-width: 990px) {
        position: fixed;
        top: 80px;
        left: 0;
        width: 100%;
        height: calc(100vh - 80px);
        background: transparent;
        opacity: 0;
        z-index: -5;
        visibility: hidden;
        transition: 0.3s ease;
        flex-direction: column;
        padding: 0 0 60px;
        justify-content: center;

        .header-logotype {
          display: none;
        }
      }

      &:before {
        content: "";
        position: fixed;
        top: -70px;
        left: 0;
        width: 100%;
        height: calc(100% + 70px);
        background-image: url("../assets/img/layout/header/main-background-mobile.jpg");
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
        @media only screen and (min-width: 990px) {
          content: none;
        }
      }
    }

    &-logotype {
      margin-top: 0;
      width: 156px;
      height: 27px;
      //width: 145px;
      //height: 90px;
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      z-index: 5;
      //margin-top: 13px;
      transition: opacity 0.3s ease;

      &:hover {
        //opacity: 0.7;
        img {
          transform: scale(1.1);
        }
      }

      @media only screen and (max-width: 990px) {
        margin-top: 0;
        width: 156px;
        height: 27px;
      }

      img {
        width: 100%;
        height: 100%;
        transition: .3s ease;
      }
    }

    &-links-block {
      display: flex;
      justify-content: space-between;
      align-items: center;
      @media only screen and (max-width: 990px) {
        flex-direction: column;
        width: 100%;
      }
    }

    // link
    // link
    &-link {
      font-weight: 700;
      font-size: 14px;
      line-height: (17/14);
      text-transform: uppercase;
      color: #ffffff;
      padding: 15px 0;
      margin: 0 15px;
      transition: 0.3s ease;
      position: relative;
      overflow: hidden;
      display: block;

      @media only screen and (max-width: 1200px) {
        margin: 0 10px;
      }

      @media only screen and (max-width: 990px) {
        margin: 0;
        padding: 20px;
      }

      &:before {
        content: "";
        position: absolute;
        bottom: 5px;
        height: 2px;
        width: 100%;
        left: -102%;
        background: #e0bc6c;
        transition: 0.3s ease;
        @media only screen and (max-width: 990px) {
          content: none;
        }
      }

      &:hover,
      &.is-active {
        color: #e0bc6c;

        &:before {
          left: 0;
        }

        .black_text {
          color: #e0bc6c;
        }
      }

      &.sign-in {
        color: #e0bc6c;
      }

      &.sign-up {
        display: flex;
        align-items: center;
        padding: 12px 22px;
        background: linear-gradient(72.27deg, #c89016 0%, #e0bc6c 101.98%);
        margin-right: 0;
        border-radius: 20px;

        @media only screen and (max-width: 990px) {
          margin: 20px 0 0;
          width: 100%;
          max-width: 230px;
          justify-content: center;
          border-radius: 20px;
        }

        &:before {
          height: 100%;
          bottom: 0;
          background: #c89016;
        }

        svg {
          margin-left: 6px;
          transition: 0.3s ease;
          position: relative;
          @media only screen and (max-width: 990px) {
            display: none;
          }
        }

        span {
          position: relative;
          z-index: 5;
          display: block;
          margin-right: 5px;
        }

        &:hover {
          color: #fff;

          svg {
            transform: translateX(5px);
          }
        }
      }
    }

    // link
    // link

    // mobile
    // mobile
    // mobile
    &-mobile {
      border-bottom: 1px solid #2c2c2c;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 80px;
      background: transparent;
      padding: 0 5px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      z-index: 60;
      background: #000;
      @media only screen and (min-width: 990px) {
        display: none;
      }

      &-btn {
        padding: 15px;
        background: transparent;
        cursor: pointer;
        position: relative;
        width: 56px;
        height: 56px;

        svg {
          position: absolute;
          top: 15px;
          left: 15px;
          transition: 0.3s ease;

          path {
            transition: 0.3s ease;
          }
        }

        &:hover {
          svg {
            path {
              stroke: #fff;
            }
          }
        }
      }

      &-burger {
        &-close {
          opacity: 0;
          visibility: hidden;
        }
      }
    }

    // mobile
    // mobile
    // mobile
  }
</style>



