<template>
  <section class="section section-banner">
    <div class="section-container">
      <div class="banner">
        <p class="title banner-title">
          {{$t('banner_2.title_1')}}
          <br>
          <span>
            {{$t('banner_2.title_2')}}
          </span>
        </p>
        <div class="banner-image-wrap">
          <img class="banner-image" src="../assets/img/pages/home/banner/banner-image-desktop.png" alt="banner-image">
        </div>
        <a href="https://backoffice.cashback-global.io/" class="button">
          <span class="button-icon">
            <img src="../assets/img/pages/home/banner/button-plus.svg" alt="button-icon">
          </span>
          <span class="button-text">
          {{$t('banner_2.button')}}
          </span>
        </a>
      </div>
    </div>
  </section>
</template>

<script>
  export default {
    name: 'Banner2',
    data() {
      return {}
    }
  }
</script>

<style lang="scss" scoped>
  .section {
    &-banner {
      background-image: url('../assets/img/pages/home/banner/banner-background.png');
      background-repeat: no-repeat;
      background-size: cover;
    }

    &-container {
      padding: 55px 20px 70px;
      width: 100%;
      max-width: 1040px;
      margin: auto;
      position: relative;
    }
  }

  .banner {
    @media (max-width: 990px) {
      display: flex;
      flex-direction: column;
      align-items: center;
    }

    &-image {
      width: 100%;
      height: 100%;
      object-fit: unset;
    }

    &-image-wrap {
      position: absolute;
      height: 320px;
      top: -30px;
      right: 20px;

      @media (max-width: 990px) {
        position: static;
        margin-bottom: 65px;
        height: auto;
        top: unset;
        right: unset;
        object-fit: contain;
      }

      @media (max-width: 600px) {
        width: 50%;
        margin: 0 auto 65px;
        position: relative;

        &:before {
          content: '';
          position: absolute;
          top: calc(100% - 30px);
          left: 50%;
          transform: translateX(-50%);
          width: 44px;
          height: 91px;
          background-image: url('../assets/img/pages/home/banner/mobile-banner-icon.png');
          background-position: center;
          background-repeat: no-repeat;
        }
      }
    }

    &-title {
      width: 100%;
      max-width: 564px;
      margin-bottom: 52px;
      color: #e0bc6c;

      @media (max-width: 600px) {
        letter-spacing: -0.02em;
      }

      @media (max-width: 400px) {
        letter-spacing: -0.02em;
      }
    }
  }
</style>



