<template>
  <div class="header-lang" v-model="$root.$i18n.locale" v-if="!this.$route.params.id">
      <span class="header-lang-visible">
      {{ $root.$i18n.locale }}
      </span>
    <div class="header-lang-open">
      <div class="header-lang-open-link"
           :class="{ 'is-active': $root.$i18n.locale === 'ru'}"
           @click="localeRu"
      >
        <img src="../assets/img/layout/header/lang/ru.svg" alt/>
        <span class="header-lang-open-desktop"></span>
        <span class="header-lang-open-mobile">ru</span>
      </div>
      <div class="header-lang-open-link"
           :class="{'is-active' : $root.$i18n.locale === 'en'}"
           @click="localeEn"
      >
        <img src="../assets/img/layout/header/lang/en.svg" alt/>
        <span class="header-lang-open-desktop">english</span>
        <span class="header-lang-open-mobile">en</span>
      </div>
    </div>
  </div>

</template>

<script>
export default {
  name: 'LocaleSwitcher',
  data() {
    return {
    };
  },
  beforeCreate: function () {
    if (localStorage.locale) {
          this.$root.$i18n.locale = localStorage.locale
        }
  },
  methods: {
    localeRu: function() {
      this.$root.$i18n.locale = 'ru';
      localStorage.locale = 'ru';
      this.localeNews();
    },
    localeEn: function() {
      this.$root.$i18n.locale = 'en';
      localStorage.locale = 'en';
      this.localeNews();
    },
    localeNews: function() {
      if (this.$route.path === '/news') {
        location.reload();
      }
    }
  },
  mounted() {
  },
}
</script>

<style lang="scss" scoped>
.header {
  &-lang {
    padding: 15px 0;
    margin: 0 15px;
    transition: 0.3s ease;
    position: relative;
    display: block;
    cursor: pointer;

    @media only screen and (max-width: 990px) {
      order: 10;
      //position: absolute;
      //bottom: 100px;
      //left: 50%;
      //transform: translateX(-50%);
      //margin: 0;
      padding: 0;
      margin-top: 30px;
    }

    &-visible {
      font-weight: 700;
      font-size: 14px;
      line-height: (17/14);
      text-transform: uppercase;
      color: #e0bc6c;
      transition: 0.3s ease;

      @media only screen and (max-width: 990px) {
        display: none;
      }
    }

    &-open {
      opacity: 0;
      z-index: -5;
      visibility: hidden;
      position: absolute;
      top: 50px;
      left: 50%;
      transform: translateX(-50%);
      width: 156px;
      background: #e9e4d9;
      box-shadow: 0 10px 15px rgba(0, 0, 0, 0.25);
      transition: 0.3s ease;

      @media only screen and (max-width: 990px) {
        position: static;
        opacity: 1;
        visibility: visible;
        transform: none;
        width: auto;
        display: flex;
        align-items: center;
        box-shadow: none;
        background: transparent;
      }

      &:before {
        content: "";
        position: absolute;
        top: -8px;
        left: 50%;
        transform: translateX(-50%);
        width: 0;
        height: 0;
        border-style: solid;
        border-width: 0 4px 8px 4px;
        border-color: transparent transparent #e9e4d9 transparent;
        @media only screen and (max-width: 990px) {
          content: none;
        }
      }

      &-link {
        width: 100%;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        padding: 10px 15px;
        position: relative;

        &:after {
          content: "";
          position: absolute;
          bottom: -0.5px;
          left: 15px;
          width: calc(100% - 30px);
          height: 1px;
          background: rgba(#a8a59e, 0.2);
        }

        &:first-child {
          padding-top: 15px;
          @media only screen and (max-width: 990px) {
            padding: 15px 7px;
          }
        }

        &:last-child {
          padding-bottom: 15px;
          @media only screen and (max-width: 990px) {
            padding: 15px 7px;
          }

          &:after {
            content: none;
          }
        }

        img {
          margin-right: 10px;
          @media only screen and (max-width: 990px) {
            display: none;
          }
        }

        span {
          font-weight: 700;
          font-size: 14px;
          line-height: (17/14);
          text-transform: uppercase;
          color: #222222;
          transition: 0.3s ease;
          @media only screen and (max-width: 990px) {
            color: #fff;
          }
        }

        &.is-active,
        &:hover {
          span {
            color: #c89016;
          }
        }
      }

      &-desktop {
        @media only screen and (max-width: 990px) {
          display: none;
        }
      }

      &-mobile {
        @media only screen and (min-width: 990px) {
          display: none;
        }
      }
    }

    &:hover {
      .header-lang-open {
        opacity: 1;
        z-index: 5;
        visibility: visible;
      }
    }
  }
}
</style>



