  /*  font-weight: 700;*/
  /*  font-style: normal;*/
  /*}*/

  // Montserrat
  @import url('https://fonts.googleapis.com/css?family=Montserrat:400,500,700&display=swap&subset=cyrillic,cyrillic-ext,latin-ext,vietnamese');
  /*@font-face {*/
  /*  font-family: 'Montserrat';*/
