  html, body {
    max-width: 100%;
    overflow-x: hidden;

    &.no-scroll {
      overflow: hidden !important;
    }

    &.pos-fixed {
      position: fixed;
      z-index: 100;
    }
  }

  body {
    font-family: 'Montserrat', sans-serif;
  }

  a,
  a:link,
  a:visited,
  a:active,
  a:hover {
    text-decoration: none;
  }

  input {
    outline: none;
    border: none;

    &::placeholder,
    &::-webkit-input-placeholder,
    &:-moz-placeholder,
    &:-moz-placeholder,
    &:-ms-input-placeholder {
      color: #A6ABB9;
      opacity: 1;
      transition: opacity 0.3s ease;
    }

    &:focus::placeholder,
    &:focus::-webkit-input-placeholder,
    &:focus::-moz-placeholder,
    &:focus:-moz-placeholder,
    &:focus:-ms-input-placeholder {
      opacity: 0;
      transition: opacity 0.3s ease;
    }
  }

  a, button, input, textarea, span {
    border: none;
    outline: none;
    outline-offset: 0;

    :active, :hover, :focus {
      border: none;
      outline: 0;
      outline-offset: 0;
    }
  }

  a, button {
    cursor: pointer;
  }

  input::-webkit-outer-spin-button,
  input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }

  .hidden {
    display: none !important;
  }

  .button-gold {
    width: 100%;
    max-width: 175px;
    min-height: 40px;
    background: linear-gradient(189.5deg, #ECBB7A 11.59%, #956F40 87%);
    border-radius: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    overflow: hidden;
    cursor: pointer;

    &:before {
      content: "";
      position: absolute;
      top: 0;
      left: -100%;
      width: 100%;
      height: 100%;
      transition: .3s ease;
      background: #956F40;
    }

    span {
      position: relative;
      z-index: 5;
      font-size: 14px;
      text-transform: uppercase;
      font-weight: 700;
      text-align: center;
      color: #FFFFFF;
      font-family: "Montserrat", sans-serif;
    }

    &:hover:before {
      left: 0;
    }

    &.disabled {
      pointer-events: none;
      background: #DADFE3;

      span {
        color: #A0A0A4;
      }
    }

    &-lg {
      min-height: 46px;
      max-width: 310px;
      margin: 0 auto;
    }
  }

</style>



