<template>
  <main-layout>
    <country-modal/>
    <section class="landing-container news-container">
      <h4 class="subtitle">
        {{$t('news.title')}}
      </h4>
      <div class="center">
        <loader v-if="loading" class="loader" />
        <div class="news-main" v-else-if="!loading && !error" :data-locale="$root.$i18n.locale">

          <div class="news-main-condition" v-if="$root.$i18n.locale === 'ru'">
            <router-link v-for="(page, idx) of items" :key="idx" :to="{ path: `news/${page.id}`}" :data-lang="page.language">
              <NewsCard :title="page.title_ru" :thumb="page.thumb_ru" :date="page.date"/>
            </router-link>
          </div>

          <div class="news-main-condition" v-else-if="$root.$i18n.locale === 'en'">
            <router-link v-for="(page, idx) of items" :key="idx" :to="{ path: `news/${page.id}`}" :data-lang="page.language">
            <NewsCard :title='page.title_en' :thumb="page.thumb_en" :date="page.date"/>
            </router-link>
          </div>

        </div>
        <h1 v-else>{{error}}</h1>
        <template v-if="!loading">
          <paginate
            v-model="page"
            :page-count="pageCount"
            :page-range="5"
            :margin-pages="2"
            :click-handler="pageChangeHandler"
            :prev-text="''"
            :next-text="''"
            :container-class="'pagination'"
            :page-class="'page-item'"
            :prev-link-class="'prev-link'"
            :next-link-class="'next-link'"
            :page-link-class="'page-link'"
          ></paginate>
        </template>
      </div>
    </section>
  </main-layout>
</template>


<script>
import MainLayout from "@/layout/MainLayout";
import Loader from "@/layout/Loader";
import axiosInstance from "@/utils/axios";
import NewsCard from "@/views/NewsCard";
import paginationMixin from "@/mixins/pagination.mixin";
import CountryModal from "../sections/CountryModal";

export default {
  metaInfo () {
    return {
      title: this.$title((localStorage.locale === 'ru') ? '' : 'News')
    }
  },
  name: "NewsPage",
  mixins: [paginationMixin],
  data: () => ({
    loading: true,
    content: null,
    error: null,
  }),

  async mounted() {

    let newsLandingLocation;

    // console.log(this.$root.$i18n.locale);

    if (window.location.origin === 'http://localhost:8080' || window.location.origin === 'http://localhost:8081') {
      newsLandingLocation = "https://backoffice.karat.jewelry";
    } else if (window.location.origin === 'https://g-dev-landing.devr.app') {
      newsLandingLocation = "https://g-dev.devr.app";
    } else if (window.location.origin === 'https://karat.jewelry') {
      newsLandingLocation = "https://backoffice.karat.jewelry";
    }

    let newsResponse = `${newsLandingLocation}/api/get-news?lang=${this.$root.$i18n.locale}`;

    // console.log(newsResponse);

    try {
      const response = await axiosInstance(
        newsResponse
      );
      this.content = response.data.data;
      this.loading = false;
    } catch (e) {
      this.loading = false;
      this.error = "-   ...";
    }
    this.setupPagination(this.content);
  },
  components: {
    Loader,
    NewsCard,
    MainLayout,
    CountryModal,
  },
  methods: {},
};
</script>

<style lang="scss" scope>
section.landing-container.news-container {
  padding: 160px 30px 60px;
  @media (max-width: 990px) {
    padding-top: 150px;
  }

  @media (max-width: 768px) {
    padding: 120px 30px 60px;
  }
  @media (max-width: 600px) {
    padding: 100px 20px 40px;
  }

  .news-main {
    width: 100%;

    /*&[data-locale='ru'] {*/
      /*a[data-lang='en'] {*/
        /*display: none;*/
      /*}*/
    /*}*/
    /*&[data-locale='en'] {*/
      /*a[data-lang='ru'] {*/
        /*display: none;*/
      /*}*/
    /*}*/
    &-condition {
      width: 100%;
      display: grid;
      grid-template-columns: 1fr 1fr 1fr;
      grid-column-gap: 25px;
      grid-row-gap: 40px;
      margin-bottom: 60px;
      @media (max-width: 990px) {
        grid-template-columns: 1fr 1fr;
      }
      @media (max-width: 600px) {
        grid-template-columns: 1fr;
        grid-row-gap: 20px;
        grid-column-gap: 20px;
        margin-bottom: 30px;
      }
    }
  }
  .subtitle {
    margin-bottom: 60px;
    font-family: "Playfair Display", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 40px;
    line-height: 1.3;
    color: #c89016;
    @media (max-width: 600px) {
      font-size: 30px;
      line-height: 1.3;
      padding: 0;
      margin-bottom: 30px;
    }
  }
  .center {
    text-align: center;
  }
  .pagination {
    display: flex;
    justify-content: center;
    color: #a6abb9;
    align-items: center;
  }

  .pagination {
    li {
      &:first-child {
        background: url("../assets/img/pages/news/prev.svg") no-repeat;
        background-position: center;
      }
      &:last-child {
        background: url("../assets/img/pages/news/next.svg") no-repeat;
        background-position: center;
      }

      &.disabled {
        filter: grayscale(100%);
        width: 34px;
        height: 34px;
        pointer-events: none;
      }
    }

    .prev-link {
      display: inline-block;
      width: 34px;
      height: 34px;
    }
    .next-link {
      display: inline-block;
      width: 34px;
      height: 34px;
    }

    .page-item {
      width: 34px;
      height: 34px;
      border-radius: 17px;
      font-family: "Montserrat", sans-serif;
      font-style: normal;
      font-weight: 600;
      font-size: 12px;
      line-height: 1.2;
      display: flex;
      align-items: center;
      text-transform: uppercase;
      justify-content: center;
      .page-link {
        width: 34px;
        height: 34px;
        display: flex;
        align-items: center;
        justify-content: center;
      }
    }
    .active {
      background: rgba(206, 155, 43, 0.15);
      color: #ce9b2b;
    }
  }
}
</style>



