<template>
  <div class="modal-country-container">
    <div class="modal-country-content" v-if="countryUsa">
      <div class="modal-overlay modal-country-overlay is-active"></div>
      <div class="modal modal-country is-active">
        <div class="modal-icon">
          <img src="../assets/img/icons/warning.svg" alt="">
        </div>
        <div class="modal-header">
          <h4 class="modal-title">
            Something Wrong...
          </h4>
          <p class="modal-text">
            Use of our service isn`t available to US citizens.
            If you are a certified investor, contact us for cooperation.
          </p>
        </div>
        <div class="modal-content">
          <span class="modal-country-mail">support@karat.jewelry</span>
        </div>
        <div class="modal-footer">
          <a href="mailto:support@karat.jewelry" class="button-gold modal-country-btn">
          <span>
              contact with support
          </span>
          </a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "@/utils/axios";

export default {
  name: 'CountryModal',
  data() {
    return {
      countryUsa: false
    }
  },
  beforeCreate() {
    axios
      .get('https://backoffice.cashback-global.io/get-ip')
      .then((response) => {
        // console.log(response);
        this.$root.country = response.data.country_code;
        // console.log(this.$root.country);
        if ('United States' === response.data.country_code) {
          this.countryUsa = true;
        }
      })
  }
}
</script>

<style lang="scss" scoped>
.modal {

  width: 92%;
  max-width: 400px;

  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 105;

  text-align: center;
  padding: 55px 30px 30px;
  border-radius: 5px;
  background-color: #fff;
  transition: 0.3s all;

  opacity: 1;
  visibility: visible;


  &-overlay {
    opacity: 1;
    visibility: visible;

    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 100;
    transition: .3s all;
    pointer-events: none;
    background: linear-gradient(180deg, #000000 0%, #473417 100%);

  }

  &-close {
    width: 35px;
    height: 35px;
    position: absolute;
    top: 3px;
    right: 3px;
    cursor: pointer;
    transition: .3s ease;

    &:before,
    &:after {
      content: "";
      position: absolute;
      top: 50%;
      left: 50%;
      width: 14px;
      height: 2px;
      background: #956F40;
    }

    &:before {
      transform: translateX(-50%) translateY(-50%) rotate(45deg);
    }

    &:after {
      transform: translateX(-50%) translateY(-50%) rotate(-45deg);
    }

    &:hover {
      opacity: .7;
    }
  }



  &-icon {
    position: absolute;
    width: 80px;
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    top: -40px;
    left: 50%;
    transform: translateX(-50%);
    border-radius: 50%;
    transition: .3s ease;
    background: linear-gradient(215.91deg, #FBF4EA 11.59%, #F0EBE3 87%);

    &:before {
      content: "";
      position: absolute;
      left: -5px;
      top: -5px;
      z-index: 115;
      width: 86px;
      height: 41px;
      border-radius: 50% 50% 0 0/100% 100% 0 0;
      border: 2px solid #ecbb7a;
      border-bottom-color: transparent;
    }
  }

  &-header {
    width: 100%;
    margin-bottom: 30px;
  }

  &-title {
    font-weight: 600;
    font-size: 22px;
    line-height: 1.3;
    text-align: center;
    color: #1C1C1F;
    display: block;
    margin-bottom: 15px;

    @media (max-width: 600px) {
      font-size: 20px;
    }
  }

  &-text {
    font-weight: 600;
    font-size: 12px;
    line-height: 1.2;
    color: #A0A0A4;
    width: 100%;

    span {
      font-weight: 700;
    }
  }

  &-content {
    width: 100%;
    margin-bottom: 30px;
  }

  &-footer {
    width: 100%;
    display: flex;
    justify-content: center;
  }

  &-image {
    width: 100%;
  }

}

//modal country

.modal-country {

  &-mail {
    background: linear-gradient(215.91deg, rgba(236, 187, 122, 0.15) 11.59%, rgba(149, 111, 64, 0.15) 87%);
    border: 1px dashed #AE8450;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 14px;
    font-weight: 600;
    font-size: 14px;
    line-height: 1.2;
    color: #AE8450;
    border-radius: 5px;
  }

  &-btn {
    max-width: 100%;

  }
}

</style>



