<template>
  <div class="news-card">
    <div class="img-news" :style="{ backgroundImage: `url(${thumb})` }">
      <div class="btn-container">
        <span class="bnt-news hidden"></span>
        <span class="date">{{date}}</span>
      </div>
    </div>
    <div class="card-title">
      <h5>{{title}}</h5>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    title: {
      type: String,
      required: true,
    },
    thumb: {
      type: String,
      required: true,
    },
    date: {
      type: String,
      required: true,
    },
  },
  data: () => ({}),
};
</script>

<style lang="scss" scoped>
/* 990, 768, 600  */
.news-card {
  &:hover {
    .card-title h5 {
      color: #c89016;
    }
  }
}
.news-card {
  min-height: 420px;
  height: 100%;
  width: 100%;
  background: #ffffff;
}
.img-news {
  position: relative;
  width: 100%;
  height: 320px;
  background: #000;
  background-image: url("../assets/img/pages/news/example1.png");
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  display: flex;
  align-items: flex-end;
  padding: 15px;
  @media (max-width: 1050px) {
    width: 100%;
    height: 360px;
    overflow: hidden;
    &:before {
      content: "";
      position: absolute;
      top: -15px;
      left: -15px;
      width: calc(100% + 30px);
      height: calc(100% + 30px);
      background-image: inherit;
      background-position: center center;
      background-repeat: no-repeat;
      background-size: cover;
      -webkit-filter: blur(15px);
      filter: blur(15px);
    }
    &:after {
      content: "";
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translateX(-50%) translateY(-50%);
      width: 100%;
      height: 100%;
      background-image: inherit;
      background-position: center center;
      background-repeat: no-repeat;
      background-size: contain;
      z-index: 4;

    }
  }

  /*&:before {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 45%;
    background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, #000000 71.35%);
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);

  }
  */
}
.card-title {
  padding: 15px;
  font-family: "Montserrat", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 1.6;
  color: #000000;
  text-align: start;
  h5 {
    transition: 0.3s ease;
  }
}
.btn-container {
  display: flex;
  //justify-content: space-between;
  justify-content: flex-end;
  align-items: center;
  width: 100%;
  box-sizing: border-box;
  position: relative;
  z-index: 5;
}
.bnt-news {
  background: none;
  padding: 5px 10px;
  border: 1px solid #e0bc6c;
  border-radius: 5px;
  display: inline-block;
  font-style: normal;
  font-weight: 700;
  font-size: 12px;
  line-height: 1.6;
  text-transform: uppercase;
  color: #e0bc6c;
}
.date {
  display: inline-block;
  font-family: "Montserrat", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 12px;
  line-height: 1.6;
  text-transform: uppercase;
  color: #e0bc6c;
  position: relative;
  z-index: 5;
}
</style>




