<template>
  <section class="section section-partner">
    <div class="partner__coin partner__coin_1" data-speed="0.5">
      <img src="../assets/img/pages/home/partner/coins/coin7.png" alt="">
    </div>
    <div class="partner__coin partner__coin_2" data-speed="0.15">
      <img src="../assets/img/pages/home/partner/coins/coin8.png" alt="">
    </div>
    <div class="section-container">
      <div class="partner">
        <h2 class="title partner-title">
          {{$t('partner.title')}}
        </h2>

        <p class="partner-text">
          {{$t('partner.desc')}}
        </p>

        <!-- TODO: task-497. link. Cashback Global sign up -->
        <a :href="cashbackGlobalRoutes.signUp" class="button partner-button">
          <span class="button-icon">
            <img src="../assets/img/pages/home/partner/button-plus.svg" alt="button-icon">
          </span>
          <span class="button-text">
            {{$t('partner.be')}}
          </span>
        </a>
      </div>
    </div>
  </section>
</template>

<script>
import cashbackGlobalRoutes from '@/config/cashbackGlobalRoutes.js';

  export default {
    name: 'Partner',
    data() {
      return {
        cashbackGlobalRoutes,
      }
    },
    mounted() {
      window.document.addEventListener('DOMContentLoaded', function () {
        const coins = document.querySelectorAll('.partner__coin');



        coins.forEach((element, index) => {
          let topPosition = +getComputedStyle(element).top.slice(0, -2);

          window.addEventListener('scroll', function () {
            let needSectionPosition = document.querySelector('.section-partner').getBoundingClientRect().top

            element.style.top = `${topPosition - ((needSectionPosition > 0) ? null : (needSectionPosition * +element.dataset.speed))}px`
          })
        })


      })
    }
  }
</script>

<style lang="scss" scoped>
  .section {
    &-partner {
      padding: 0;
      position: relative;
    }

    &-container {
      background-image: url('../assets/img/pages/home/partner/partner-image.png');
      background-color: #fff;
      background-repeat: no-repeat;
      background-position: right;
      background-size: contain;
      padding: 126px 0 123px 209px;
      position: relative;

      &:before {
        content: '';
        position: absolute;
        bottom: 157px;
        right: 15.5%;
        width: 565px;
        height: 148px;
        background-image: url('../assets/img/pages/home/partner/gold.svg');
        opacity: 0.5;
      }


      @media (max-width: 990px){
        padding: 100px 0;
        background-size: cover;
      }
      @media (max-width: 600px){
        padding: 0;
        background-image: unset;
      }
    }
  }

  .partner {
    padding: 41px 35px 52px 49px;
    width: 100%;
    max-width: 520px;
    background-color: #f4f6f8;
    position: relative;

    &:before {
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      width: 230px;
      height: 292px;
      background-image: url('../assets/img/pages/home/partner/partner-icon.svg');
      background-repeat: no-repeat;
      background-position: center;
    }

    @media (max-width: 990px) {
      margin: auto;
    }

    @media (max-width: 600px) {
      max-width: 600px;
      padding: 30px 20px;
      background-color: #fff;

      &:before {
        width: 172px;
        height: 219px;
        background-image: url('../assets/img/pages/home/partner/partner-icon-mobile.svg');
      }
    }


    &-title {
      margin-bottom: 30px;
      position: relative;
      z-index: 1;

      @media (max-width: 600px) {
        margin-bottom: 20px;
      }
    }

    &-text {
      color: #222;
      font-size: 16px;
      line-height: (27 / 16);
      font-weight: 700;
      margin-bottom: 20px;

      @media (max-width: 600px) {
        font-size: 14px;
        line-height: (27 / 14);
      }
    }

    &-button {
      @media (max-width: 990px) {
        margin: auto;
      }
    }


     &__coin {
      position: absolute;
      z-index: 1;
      transition: .05s;

      img {
        width: 100%;
      }

      &_1 {
        left: -140px;
        top: 20px;
      }

      &_2 {
        left: 130px;
        top: 630px;
      }

      @media (max-width: 1500px) {
      }

      @media (max-width: 1200px) {
        display: none;
      }
    }
  }
</style>



