</template>

<script>
  export default {
    name: 'Cashback',
    data: function() {
      return {
        isActive: false,
      }
    },
    mounted() {
      window.document.addEventListener('DOMContentLoaded', function () {
        const coins = document.querySelectorAll('.cashback__coin');



        coins.forEach((element, index) => {
          let topPosition = +getComputedStyle(element).top.slice(0, -2);

          window.addEventListener('scroll', function () {
            let needSectionPosition = document.querySelector('.section-cashback').getBoundingClientRect().top

            element.style.top = `${topPosition - ((needSectionPosition > 0) ? null : (needSectionPosition * +element.dataset.speed))}px`
          })
        })


      })
    },
    methods: {
      openModal: function () {
         this.isActive = true;
         const videoFrame = document.getElementById('modal_video_iframe_cashback');
         const videoUrl = videoFrame.getAttribute('data-src');
         videoFrame.setAttribute('src', videoUrl);
      },

      closeModal: function () {
        this.isActive = false;
        const videoFrame = document.getElementById('modal_video_iframe_cashback');
        videoFrame.setAttribute('src', '');
      }
    }
  }

</script>

