<template>
  <main-layout>
    <section class="landing-container news-single-container">
      <h4 class="subtitle">{{$t('news.title')}}</h4>
      <div class="loader-center">
        <loader v-if="loading" class="loader" />
        <div v-else-if="!loading && !error">
          <NewsArticle :info="info" />
          <router-link to="/news" class="button bnt-back">
            <span>{{$t('news.see_all')}}</span>
          </router-link>
        </div>
        <p v-else class="error">{{error}}</p>
      </div>
    </section>
  </main-layout>
</template>

<script>
import MainLayout from "@/layout/MainLayout";
import Loader from "../layout/Loader";
import axiosInstance from "../utils/axios";
import NewsCard from "@/views/NewsCard";
import NewsArticle from "@/views/NewsArticle";

export default {
    metaInfo () {
    return {
      title: this.$title((localStorage.locale === 'ru') ? '' : 'News')
    }
  },
  name: "NewsPage",
  data: () => ({
    loading: true,
    error: null,
    info: null
  }),

  async mounted() {

    let newsLandingLocation;

    if (window.location.origin === 'http://localhost:8080' || window.location.origin === 'http://localhost:8081') {
      // local test with prod news
      newsLandingLocation = "https://backoffice.karat.jewelry";
    } else if (window.location.origin === 'https://g-dev-landing.devr.app') {
      newsLandingLocation = "https://g-dev.devr.app";
    } else if (window.location.origin === 'https://karat.jewelry') {
      newsLandingLocation = "https://backoffice.karat.jewelry";
    }

    let newsResponse = `${newsLandingLocation}/api/get-news-content/${this.$route.params.id}`;

    try {

      if (this.$route.params && this.$route.params.id) {

        const id = `id=${this.$route.params.id}`;
        const response = await axiosInstance(
          `${newsResponse}?${id}`
        );

        // console.log(response);

        if (this.$root.$i18n.locale === 'ru') {
          this.info = {
            content: response.data[0].content_ru,
            thumb: response.data[0].thumb_ru,
            title: response.data[0].title_ru,
            date: response.data[0].date,
          }
        } else {
          this.info = {
            content: response.data[0].content_en,
            thumb: response.data[0].thumb_en,
            title: response.data[0].title_en,
            date: response.data[0].date,
          }
        }

        // console.log(this.info);

        this.loading = false;
      }
    } catch (e) {
      this.$router.push("/news")
      this.loading = false;
      this.error = "-    ...";
    }

  },
  components: {
    Loader,
    NewsArticle,
    MainLayout
  },
  methods: {}
};
</script>

<style lang="scss" scoped>
section.landing-container.news-single-container {
  padding: 160px 30px 60px;
  @media (max-width: 990px) {
    padding-top: 150px;
  }
  @media (max-width: 768px) {
    padding: 120px 30px 60px;
  }
  @media (max-width: 600px) {
    padding: 100px 20px 40px;
  }
  .error {
    font-family: "Playfair Display", sans-serif;
    font-weight: 700;
    line-height: 1.3;
    color: #000000;
  }
  .subtitle {
    margin-bottom: 60px;
    font-family: "Playfair Display", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 40px;
    line-height: 53px;
    color: #c89016;
    @media (max-width: 768px) {
      font-size: 30px;
      line-height: 1.3;
      padding: 0;
      margin-bottom: 20px;
    }
  }
  .loader-center {
    text-align: center;
  }
  .bnt-back {
    background: linear-gradient(
      57.31deg,
      #c89016 1.17%,
      #ce9b2b 32.12%,
      #e0bc6c 90.48%
    );
    padding: 15px 65px;

    border-radius: 30px;
    display: inline-block;
    color: #ffffff;
    max-width: 295px;
  }

  .button.bnt-back span {
    position: relative;
    z-index: 5;
    font-weight: 700;
    font-size: 14px;
    line-height: 1.2;
    letter-spacing: 0.15em;
    text-transform: uppercase;
    font-family: "Montserrat", sans-serif;
    font-style: normal;
  }
}
</style>



