<template>
  <section class="section section-statistics">
    <div class="section-container">
      <div class="statistics">
        <div class="statistics-block statistics-block-year">
          <div class="statistics-icon statistics-icon-year"></div>
          <h3 class="statistics-title statistics-title-year"> <span>15</span> </h3>
          <p class="statistics-text statistics-text-year">  </p>
        </div>
        <div class="statistics-block statistics-block-users">
          <div class="statistics-icon statistics-icon-users"></div>
          <h3 class="statistics-title statistics-title-users"> <span>100,000</span> </h3>
          <p class="statistics-text statistics-text-users">   </p>
        </div>
        <div class="statistics-block statistics-block-bonuses">
          <div class="statistics-icon statistics-icon-bonuses"></div>
          <h3 class="statistics-title statistics-title-bonuses"> <span>1,000,000</span> </h3>
          <p class="statistics-text statistics-text-bonuses">    </p>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
  export default {
    name: 'Statistics',
    data() {
      return {}
    }
  }
</script>

<style lang="scss" scoped>
  .section {
    &-statistics {
      background-image: url('../assets/img/pages/home/statistics/background-statistics.jpg');
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
      padding: 0;
    }

    &-container {
      width: 100%;
      max-width: 1040px;
      padding: 40px 20px;
      margin: auto;
    }
  }

  .statistics {
    display: flex;
    align-items: center;
    justify-content: space-between;

    @media (max-width: 768px) {
      flex-direction: column;
    }

    &-block {
      width: 272px;

      @media (max-width: 768px) {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-bottom: 30px;
        width: 100%;

        &:last-child {
          margin-bottom: 0;
        }
      }
    }

    &-icon {
      width: 52px;
      height: 52px;
      border: 2px solid #e0bc6c;
      transform: translate(9px, 9px) rotate(45deg);
      position: relative;

      @media (max-width: 768px) {
        transform: translate(0, 9px) rotate(45deg);
      }

      &:before {
        content: '';
        position: absolute;
        width: 52px;
        height: 52px;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%) rotate(-45deg);
        background-color: rgba(255, 255, 255, 0.1);
        backdrop-filter: blur(8px);
      }

      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%) rotate(-45deg);
        background-position: center;
        background-repeat: no-repeat;
      }

      &-year:after {
        background-image: url('../assets/img/pages/home/statistics/award-icon.svg');
        width: 24px;
        height: 24px;
      }

      &-users:after {
        background-image: url('../assets/img/pages/home/statistics/users-icon.svg');
        width: 24px;
        height: 24px;
      }

      &-bonuses:after {
        background-image: url('../assets/img/pages/home/statistics/wallet-icon.svg');
        width: 19px;
        height: 19px;
      }
    }

    &-title {
      font-family: 'Playfair Display', sans-serif;
      color: #e0bc6c;
      font-size: 25px;
      line-height: (33 / 25);
      font-weight: 700;
      margin-bottom: 15px;
      margin-top: 20px;



      &-year {
        width: 113px;
      }

      &-users {
        width: 235px;
      }

      &-bonuses {
        width: 272px;
      }

      & span {
        font-size: 33px;
        line-height: (53 / 33);
      }

      @media (max-width: 768px) {
        width: 100%;
        text-align: center;
      }
    }

    &-text {
      color: #fff;
      font-size: 16px;
      line-height: (24 / 16);
      font-weight: 500;

      &-year {
        width: 200px;
      }

      &-users {
        width: 163px;
      }

      &-bonuses {
        width: 238px;
      }

      @media (max-width: 768px) {
        width: 100%;
        text-align: center;
      }
    }
  }
</style>



