<template>
  <section class="section section-calc">
    <div class="section-container">
      <h2 class="title calc-title">
        {{ $t('calculator.title') }}
      </h2>
      <div class="calc">
        <div class="calc-steps">

          <div class="calc-step">
            <div class="calc-step-header">
              <div class="calc-step-count">
                <span>1</span>
              </div>
              <h6 class="calc-step-title">
                {{ $t('calculator.type') }}
              </h6>
            </div>
            <div class="calc-step-choose">
              <div class="calc-step-choose-select"
                   :class="calculateData.type === 'certificate' ? 'is-active' : ''"
                   v-on:click="stepDisabled = false,
                   handleTabsType('certificate'),
                   activeCert = true">
                <span class="calc-step-choose-select-name">
                  {{ $t('calculator.certificate') }}
                </span>
              </div>
              <div class="calc-step-choose-select"
                   :class="calculateData.type === 'vaucher' ? ' is-active' : ''"
                   v-on:click="handleTabsType('vaucher'),
                   activeCert = false,
                   stepDisabled = true">
                <span class="calc-step-choose-select-name">
                  {{ $t('calculator.vaucher') }}
                </span>
              </div>
            </div>
          </div>

          <div class="calc-step"
               :class="stepDisabled === true ? 'disabled' : '' ">
            <div class="calc-step-header">
              <div class="calc-step-count">
                <span>2</span>
              </div>
              <h6 class="calc-step-title">
                {{ $t('calculator.material') }}
              </h6>
            </div>
            <div class="calc-step-choose">
              <div class="calc-step-choose-select"
                   :class="calculateData.type === 'vaucher' ? '' : calculateData.material === 'silver' ? 'is-active' : ''"
                   v-on:click="handleTabsMaterial('silver')">
                <span class="calc-step-choose-select-name">
                {{ $t('calculator.silver') }}
                </span>
              </div>
              <div class="calc-step-choose-select"
                   :class="calculateData.type === 'vaucher' ? '' : calculateData.material === 'gold' ? 'is-active' : ''"
                   v-on:click="handleTabsMaterial('gold')">
                <span class="calc-step-choose-select-name">
                {{ $t('calculator.gold') }}
                </span>
              </div>
            </div>
          </div>

          <div class="calc-step">
            <div class="calc-step-header"
                 :class="stepDisabled === true ? 'disabled' : '' "
            >
              <div class="calc-step-count">
                <span>3</span>
              </div>
              <h6 class="calc-step-title">
                {{ $t('calculator.period') }}
              </h6>
            </div>
            <div class="calc-step-choose calc-step-choose-period">
              <div class="calc-step-choose-select calc-step-choose-period-select"
                   :class="[calculateData.type === 'vaucher' ? '' : calculateData.period === 7 ? 'is-active' : '',
                   stepDisabled === true ? 'is-active' : ''
                   ]"
                   v-on:click="handleTabsPeriod(7), periodFooter = 7">
                <span class="calc-step-choose-select-name calc-step-choose-period-select-value">7</span>
                <span class="calc-step-choose-select-name calc-step-choose-period-select-name">
                {{ $t('calculator.days') }}
                </span>
              </div>
              <div class="calc-step-choose-select calc-step-choose-period-select"
                   v-bind:class="[
                      calculateData.type === 'vaucher' ? '' : calculateData.period === 30 ? 'is-active' : '',
                      stepDisabled === true ? 'disabled' : ''
                    ]"
                   v-on:click="handleTabsPeriod(30), periodFooter = 30">
                <span class="calc-step-choose-select-name calc-step-choose-period-select-value">1</span>
                <span class="calc-step-choose-select-name calc-step-choose-period-select-name">
                {{ $t('calculator.month') }}
                </span>
              </div>
              <div class="calc-step-choose-select calc-step-choose-period-select"
                   :class="[
                     calculateData.type === 'vaucher' ? '' : calculateData.period === 90 ? 'is-active' : '',
                     stepDisabled === true ? 'disabled' : ''
                     ]"

                   v-on:click="handleTabsPeriod(90), periodFooter = 90">
                <span class="calc-step-choose-select-name calc-step-choose-period-select-value">3</span>
                <span class="calc-step-choose-select-name calc-step-choose-period-select-name">
                  {{ $t('calculator.months') }}
                </span>
              </div>
            </div>
          </div>

          <div class="calc-step">
            <div class="calc-step-header">
              <div class="calc-step-count">
                <span>4</span>
              </div>
              <h6 class="calc-step-title">
                {{ $t('calculator.sum') }}, USD</h6>
            </div>
            <div class="calc-step-choose calc-step-choose-sum">
              <button class="calc-step-choose-minus" v-on:click="decrementSum"></button>
              <label>
                <input class="calc-step-choose-amount"
                       :rules="sum.numberRules"
                       v-model="sum.current"
                       v-on:input="sumChange"
                       :placeholder="sum.init"
                       @keypress="isNumber($event)">
              </label>
              <button class="calc-step-choose-plus" v-on:click="incrementSum"></button>
            </div>
          </div>
        </div>

        <div class="calc-footer">
          <h6 class="calc-footer-title">
            {{ $t('calculator.your_cashback') }}
          </h6>
          <div class="calc-footer-content">

            <div v-if="!activeCert" class="calc-footer-block">
              <p class="calc-footer-number">{{ output.week }}</p>
              <p class="calc-footer-text">{{ $t('calculator.once_7d') }}</p>
            </div>

            <div v-else-if="activeCert" class="calc-footer-block">
              <div v-if="periodFooter === 7">
                <p class="calc-footer-number">{{ output.week }}</p>
                <p class="calc-footer-text">{{ $t('calculator.once_7d') }}</p>
              </div>
              <div v-else-if="periodFooter === 30">
                <p class="calc-footer-number">{{ output.month_1 }}</p>
                <p class="calc-footer-text">{{ $t('calculator.once_1m') }}</p>
              </div>
              <div v-else-if="periodFooter === 90">
                <p class="calc-footer-number">{{ output.month_3 }}</p>
                <p class="calc-footer-text">{{ $t('calculator.once_3m') }}</p>
              </div>
            </div>


            <div class="calc-footer-block">
              <p class="calc-footer-number">{{ output.year }}</p>
              <p class="calc-footer-text">{{ $t('calculator.once_1y') }}</p>
            </div>
          </div>
        </div>

      </div>
    </div>
  </section>
</template>

<script>
import axios from "@/utils/axios";

export default {
  name: 'Calculator',
  data() {
    return {
      state: {},
      calculateData: {
        type: 'certificate',
        material: 'gold',
        period: 7
      },
      sum: {
        min: 100,
        max: 10000000,
        init: 100,
        current: 100,
        errorName: ' ',
      },
      calcTypeActiveItem: null,
      output: {
        week: -1,
        month_1: -1,
        month_3: -1,
        year: -1,
      },
      periodFooter: 7,
      activeCert: true,
      stepDisabled: false,
    }
  },
  methods: {
    isNumber: function (evt) {
      evt = (evt) ? evt : window.event;
      const charCode = (evt.which) ? evt.which : evt.keyCode;

      if ((charCode > 31 && (charCode < 48 || charCode > 57)) && charCode !== 46) {
        evt.preventDefault();
      } else {
        return true;
      }
    },
    handleTabsType: function (type) {
      this.calculateData.type = type;

      this.outputData();
    },
    handleTabsMaterial: function (material) {
      this.calculateData.material = material;

      this.outputData();
    },
    handleTabsPeriod: function (period) {
      this.calculateData.period = period;

      this.outputData();
    },
    validateSum: function () {
      const val = this.sum.current

      // console.log(val)

      if (this.sum.current < this.sum.min) {
        this.sum.current = this.sum.min;
      }

      if (this.sum.current > this.sum.max) {
        this.sum.current = this.sum.max;
      }

      if (val.indexOf('.') !== -1) {
        this.sum.current = val.replace(/\./g, "");
      }
    },
    decrementSum: function () {
      if (this.sum.current !== this.sum.min) {
        this.sum.current = this.sum.current - 1;
      }

      this.outputData();
    },
    incrementSum: function () {
      if (this.sum.current !== this.sum.max) {
        this.sum.current = this.sum.current + 1;
      }

      this.outputData();
    },
    sumChange: function () {
      this.validateSum()
      this.outputData();
    },
    getProductsCoef() {
      return axios
        .get('https://backoffice.cashback-global.io/api/get-deposit-products/karat-jewelry')
        .then(response => {
          this.state = response.data.products;
        })
        .catch(error => console.log(error));
    },
    outputData: function () {
      const {current} = this.sum;
      this.getProductsCoef().then(() => {
        if (this.calculateData.type === 'certificate') {
          if (this.calculateData.material === 'silver') {
            if (this.calculateData.period === 7) {
              // console.log('cert-silver-7')
              this.output.year = (current * this.state['certificate-silver'][1].percentage).toFixed(2)
              this.output.week = (this.output.year / 52).toFixed(2)
            } else if (this.calculateData.period === 30) {
              // console.log('cert-silver-30')
              this.output.year = (current * this.state['certificate-silver'][2].percentage).toFixed(2)
              this.output.month_1 = (this.output.year / 12).toFixed(2)
            } else if (this.calculateData.period === 90) {
              // console.log('cert-silver-90')
              this.output.year = (current * this.state['certificate-silver'][3].percentage).toFixed(2)
              this.output.month_3 = (this.output.year / 4).toFixed(2)
            }
          }
          if (this.calculateData.material === 'gold') {
            if (this.calculateData.period === 7) {
              // console.log('cert-gold-7')
              this.output.year = (current * this.state['certificate-gold'][1].percentage).toFixed(2)
              this.output.week = (this.output.year / 52).toFixed(2)
            } else if (this.calculateData.period === 30) {
              // console.log('cert-gold-30')
              this.output.year = (current * this.state['certificate-gold'][2].percentage).toFixed(2)
              this.output.month_1 = (this.output.year / 12).toFixed(2)
            } else if (this.calculateData.period === 90) {
              // console.log('cert-gold-90')
              this.output.year = (current * this.state['certificate-gold'][3].percentage).toFixed(2)
              this.output.month_3 = (this.output.year / 4).toFixed(2)
            }
          }
        }
        if (this.calculateData.type === 'vaucher') {
          let vaucherCoff = 0;
          if (current < 500) {
            vaucherCoff = this.state.voucher[1].percentage
          } else if (current >= 500 && current < 1500) {
            vaucherCoff = this.state.voucher[2].percentage
          } else if (current >= 1500 && current < 5000) {
            vaucherCoff = this.state.voucher[3].percentage
          } else if (current >= 5000 && current < 10000) {
            vaucherCoff = this.state.voucher[4].percentage
          } else if (current >= 10000) {
            vaucherCoff = this.state.voucher[5].percentage
          }

          this.output.year = (current * vaucherCoff).toFixed(2);
          this.output.week = (this.output.year / 52).toFixed(2);

        }
      });
    },
  },
  mounted() {
    this.outputData();
  }
}
</script>

