    padding: 0;
    background-image: url('../assets/img/pages/home/calculator/background-calculator.jpg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    position: relative;

    &:before {
      content: '';
      position: absolute;
      left: 85px;
      bottom: 0;
      width: 554px;
      height: 554px;
      background-image: url('../assets/img/pages/home/calculator/calculator-image.png');
      background-repeat: no-repeat;
      background-position: center;
      z-index: 1;

      @media (max-width: 990px) {
        content: unset;
      }
    }

    &:after {
      content: '';
      position: absolute;
      left: 222px;
      bottom: 190px;
      width: 565px;
      height: 147px;
      background-image: url('../assets/img/pages/home/calculator/gold.svg');
      background-repeat: no-repeat;
      background-position: center;

      @media (max-width: 990px) {
        content: unset;
      }
    }
  }

  &-container {
    width: 100%;
    max-width: 1040px;
    margin: auto;
    padding: 120px 20px 120px;
    display: flex;

    @media (max-width: 990px) {
      padding: 30px 20px;
      flex-direction: column;
      align-items: center;

      &:before {
        content: '';
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        top: 38px;
        width: 554px;
        height: 554px;
        background-image: url('../assets/img/pages/home/calculator/calculator-image.png');
        background-repeat: no-repeat;
        background-position: center;
        z-index: 1;

        @media (max-width: 410px) {
          left: -60px;
          top: 58px;
          transform: translateX(0);
        }
      }
    }
  }
}

