  max-width: 480px;
  position: relative;
  z-index: 1;

  &-title {
    width: 100%;
    max-width: 440px;
    margin-right: 80px;
    font-size: 40px;
    line-height: (53 / 40);

    @media (max-width: 990px) {
      margin-right: 0;
      margin-bottom: 160px;
      font-size: 30px;
      line-height: 40px;
    }
  }

  &-steps {
    padding: 30px 32px 45px;
    background: #fff;
  }

  &-step {
