      }
    }

    &-count {
      width: 46px;
      height: 46px;
      background-image: url('../assets/img/pages/home/calculator/bg-icon.png');
      background-repeat: no-repeat;
      background-position: center;
      margin-right: 22px;
      position: relative;
      display: flex;
      align-items: center;
      justify-content: center;

      & span {
        position: relative;
        z-index: 1;
        font-size: 18px;
        line-height: 1.2;
        color: #c89016;
        font-family: 'Playfair Display', sans-serif;
        font-weight: 700;
        bottom: 2px;
      }

      &:before {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 28px;
        height: 28px;
        border: 2px solid #c89016;
      }
    }

    &-title {
      font-weight: 700;
      font-size: 16px;
      line-height: 1.5;
      color: #222222;

      @media (max-width: 600px) {
