        justify-content: center;
        align-items: center;
        cursor: pointer;
        transition: opacity .3s ease;
        border: none;
        user-select: none;

        &:hover {
          opacity: 0.7;
        }

        &.is-active {
          background: #F8F0DF;
          box-shadow: 0 5px 10px rgba(248, 240, 223, 0.8);

          .calc-step-choose-select-name {
            color: #C89016;
          }
        }

        &-name {
          font-weight: 500;
          font-size: 18px;
          color: #B1B4BD;
          pointer-events: none;

          @media (max-width: 600px) {
            font-size: 14px;
          }
        }


      }

      &-period {
        grid-template-columns: 1fr 1fr 1fr;

        &-select {
          min-height: 55px;
          flex-direction: column;
          padding: 5px;

          &.disabled {
            opacity: .5;
            pointer-events: none;
          }

          &-value {
            font-weight: 700;
            font-size: 20px;
            margin-bottom: 3px;
            display: block;
            @media(max-width: 600px) {
              font-size: 17px;
            }
          }

          &-name {
            font-weight: 700;
            font-size: 14px;
            @media(max-width: 600px) {
              font-size: 12px;
            }
          }
        }


      }

      &-sum {
        display: flex;
        justify-content: space-between;
        align-items: center;
        position: relative;
        border-top: 1px solid rgba(137, 142, 150, 0.1);
