<template>
  <section id="about_us" class="section section-about-us">

    <div class="about-us__ring about-us__ring_1" data-speed="0.4">
      <img src="../assets/img/pages/home/about-us/fly-rings/fly-ring1.png" alt="">
    </div>
    <div class="about-us__ring about-us__ring_2" data-speed="0.25">
      <img src="../assets/img/pages/home/about-us/fly-rings/fly-ring2.png" alt="">
    </div>

    <div class="landing-container section-container">
      <div class="about-us">

        <div class="about-us-content">
          <div class="about-us-content-text-box">
            <h2 class="title about-us-content-title">
              {{$t('about_us.title')}}
            </h2>
            <p class="about-us-content-text">
              {{$t('about_us.desc')}}
            </p>
          </div>
          <div class="about-us-video">
            <div class="about-us-video-block"></div>

            <div class="about-us-video-btn-area">
              <div class="about-us-video-btn" @click="openModal"></div>
              <span class="about-us-video-watch-text">
                    {{$t('about_us.watch_video')}}
                </span>
            </div>


          </div>
        </div>
        <div class="about-us-description">
          <p class="about-us-description-text">
            {{$t('about_us.text_1')}}
          </p>
          <p class="about-us-description-text about-us-description-text_2">
            {{$t('about_us.text_2')}}
            {{$t('about_us.text_3')}}

            <a class="about-us-description-link" :href="cashbackGlobalRoutes.landing">Cashback Global.</a>
          </p>
        </div>
      </div>
    </div>
    <div v-bind:class="['overlay', 'js-overlay-modal', {'active': isActive}]" id="overlay" @click="closeModal"></div>
    <div v-bind:class="['modal', {'active': isActive}]">
      <div class="modal-close" @click="closeModal"></div>
      <h3 class="modal-text">{{$t('about_us.modal_title')}}

      </h3>
      <div class="modal-video">

        <iframe id="modal_video_iframe_about-us" v-if="$i18n.locale === 'ru'" class="modal-video-iframe" src=""
                data-src="https://www.youtube.com/embed/DS-Ksljafw0?autoplay=1" frameborder="0"
                allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
                allowfullscreen></iframe>

        <iframe id="modal_video_iframe_about-us" v-else class="modal-video-iframe" src=""
                data-src="https://www.youtube.com/embed/wgf3-8jTtlU?autoplay=1" frameborder="0"
                allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
                allowfullscreen></iframe>

      </div>
    </div>
  </section>
</template>

<script>
import cashbackGlobalRoutes from '@/config/cashbackGlobalRoutes.js';

  export default {
    name: 'AboutUs',
    data: function () {
      return {
        isActive: false,
        cashbackGlobalRoutes,
      }
    },
    mounted() {
      window.document.addEventListener('DOMContentLoaded', function () {
        const coins = document.querySelectorAll('.about-us__ring');


        coins.forEach((element, index) => {
          let topPosition = +getComputedStyle(element).top.slice(0, -2);

          window.addEventListener('scroll', function () {
            let needSectionPosition = document.querySelector('.section-about-us').getBoundingClientRect()
              .top

            element.style.top =
              `${topPosition - ((needSectionPosition > 0) ? null : (needSectionPosition * +element.dataset.speed))}px`
          })
        })


      })
    },
    methods: {
      openModal: function () {
        this.isActive = true;
        const videoFrame = document.getElementById('modal_video_iframe_about-us');
        const videoUrl = videoFrame.getAttribute('data-src');
        videoFrame.setAttribute('src', videoUrl);
      },

      closeModal: function () {
        this.isActive = false;
        const videoFrame = document.getElementById('modal_video_iframe_about-us');
        videoFrame.setAttribute('src', '');
      }
    }
  }

</script>

