<template>
  <section class="article-main">
    <div class="article-img" :style="{ backgroundImage: `url(${info.thumb})` }"></div>
    <article class="blog">
      <div class="top-container">
        <span class="bnt-news hidden"></span>
        <span class="date">{{info.date}}</span>
      </div>
      <h4 class="title">{{info.title}}</h4>
      <span v-html="info.content"></span>
    </article>
  </section>
</template>

<script>
import MainLayout from "@/layout/MainLayout";
import Loader from "../layout/Loader";
import axiosInstance from "../utils/axios";

export default {
  name: "NewsArticle",
  props: {
    info: {
      type: Object,
      required: true,
    },
  },
  data: () => ({
    loading: true,
    content: false,
    error: null,
  }),

  async mounted() {},
  components: {
    Loader,
    MainLayout,
  },
  methods: {
    first() {
      axiosInstance().then((response) => {
        this.content = response.data.data;
        this.loading = false;
      });
    },
  },
};
</script>

<style lang="scss">

.article-main {
  display: flex;
  justify-content: space-between;
  padding: 20px;
  background: #ffffff;
  margin-bottom: 60px;
  @media (max-width: 900px) {
    flex-direction: column;
    padding: 20px 20px 0;
  }
  @media (max-width: 600px) {
    padding: 0;
    margin-bottom: 40px;
  }

  .article-img {
    width: 100%;
    max-width: calc(40% - 20px);
    height: 360px;
    background: #000;
    //background-image: url("../assets/img/pages/news/articl-img.png");
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    position: relative;
    @media (max-width: 900px) {
      max-width: 100%;
      height: 360px;
      overflow: hidden;
      &:before {
        content: "";
        position: absolute;
        top: -15px;
        left: -15px;
        width: calc(100% + 30px);
        height: calc(100% + 30px);
        background-image: inherit;
        background-position: center center;
        background-repeat: no-repeat;
        background-size: cover;
        -webkit-filter: blur(15px);
        filter: blur(15px);
      }
      &:after {
        content: "";
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translateX(-50%) translateY(-50%);
        width: 100%;
        height: 100%;
        background-image: inherit;
        background-position: center center;
        background-repeat: no-repeat;
        background-size: contain;
        z-index: 5;

      }
    }
  }
}

.blog {
  width: 60%;
  text-align: left;

  @media (max-width: 900px) {
    width: 100%;
    padding: 30px 0;
  }


  @media (max-width: 600px) {
    padding: 30px 20px;
  }

  .top-container {
    padding-bottom: 20px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    width: 100%;
    box-sizing: border-box;
    font-family: "Montserrat", sans-serif;
    font-style: normal;
    font-size: 14px;
    line-height: 1.4;

    .bnt-news {
      display: inline-block;
      font-size: 14px;
      line-height: 1.4;
      text-transform: uppercase;
      color: #c89016;
      font-weight: 700;
    }
    .date {
      display: inline-block;
      font-size: 14px;
      line-height: 1.4;
      text-transform: uppercase;
      color: #c89016;
      font-weight: 700;
    }
  }

  * {
    font-weight: 500;
    font-size: 14px;
    color: #000000;

    line-height: 1.4 !important;
    font-family: "Montserrat", sans-serif !important;
  }

  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    font-family: "Playfair Display", sans-serif;
    font-weight: 700;
    line-height: 1.3;
    color: #000000;
  }

  h1 {
    font-size: 25px;
  }
  h2 {
    font-size: 24px;
  }
  h3 {
    font-size: 22px;
  }
  h4 {
    font-size: 22px;
  }
  h5,
  h6 {
    font-size: 20px;
  }

  a {
    color: #c89016 !important;
    transition: opacity 0.3s ease;
    &:hover {
      opacity: 0.8;
    }
  }
  ul {
    padding-left: 10px;
    list-style-type: none;

    li {
      line-height: 1.4;
      padding: 0 0 0 15px;
      margin-bottom: 10px;
      color: #ce9b2b;
      font-size: 20px;
      position: relative;
      &:before {
        content: "";
        position: absolute;
        top: 12px;
        left: 0;
        width: 6px;
        height: 6px;
        border-radius: 50%;
        background: #ce9b2b;
      }

      span {
        color: #1c1c1f;
        font-weight: 500;
        font-size: 14px;
      }
    }
  }

  .title {
    font-family: "Playfair Display", sans-serif !important;
    font-weight: bold;
    font-size: 25px;
    line-height: 1.3;
    margin-bottom: 20px;
    color: #000000;
  }

  p {
    font-weight: 500;
    font-size: 14px;
    line-height: 1.9;
    color: #000000;
  }
}
</style>




