<template>
  <div class="layout">
    <Header></Header>

    <transition name="fade" appear>
      <main class="content" :class="{ theme: dark_theme }">
        <slot></slot>
      </main>
    </transition>

    <Footer></Footer>
  </div>
</template>

<script>
import Header from "@/layout/Header";
import Footer from "@/layout/Footer";

export default {
  name: "MainLayout",
  data() {
    return {
      dark_theme: false
    };
  },
  components: {
    Header,
    Footer
  },
  mounted() {
    if (
      this.$route.matched[0].path === "/news" ||
      this.$route.matched[0].path === "/news/:id"
    ) {
      this.dark_theme = true;
    }
  },
};
</script>

<style lang="scss">
//.content {
  //min-height: 100vh;
//}

.theme {
  background:#F4F6F8;
}

.section {
  width: 100%;
}

.landing {
  overflow: hidden;

  * {
    box-sizing: border-box;
  }

  &-container {
    width: 100%;
    max-width: 1060px;
    padding: 0 30px;
    margin: 0 auto;
    @media only screen and (max-width: 990px) {
      padding: 0 20px;
    }
  }

  &-title {
    font-family: "Playfair Display", sans-serif;
    font-weight: 700;
    font-size: 50px;
    line-height: 1.1;
    color: #c89016;
    margin-bottom: 40px;
    position: relative;

    @media only screen and (max-width: 990px) {
      font-size: 42px;
    }
    @media only screen and (max-width: 600px) {
      font-size: 35px;
    }
  }
}

.title {
  font-family: "Playfair Display", serif;
  font-size: 35px;
  line-height: calc(47 / 35);
  color: #c89016;
  margin-bottom: 52px;
  font-weight: 700;

  @media (max-width: 600px) {
    font-size: 30px;
    line-height: calc(40 / 30);
  }

  & span {
    color: #fff;
  }
}

.button {
  display: flex;
  width: 100%;
  max-width: 255px;
  background: linear-gradient(
    59.81deg,
    #c89016 1.17%,
    #ce9b2b 32.12%,
    #e0bc6c 90.48%
  );
  border-radius: 50px;
  border: none;
  align-items: center;
  justify-content: center;
  position: relative;
  padding: 18px 9px 18px 42px;
  overflow: hidden;

  @media (max-width: 990px) {
    max-width: 295px;
  }

  &-sm {
    max-width: 225px;
  }

  &:before {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    width: 0;
    height: 0;
    background-color: #a87401;
    transition: 0.3s ease;
    border-radius: 50px;
  }

  &:hover {
    &:before {
      width: 100%;
      height: 100%;
    }
  }

  &:focus,
  &:active {
    /*box-shadow: 0 4px 10px rgba(28, 28, 28, 0.6);*/
  }

  &-dark {
    background: #282828;
    padding: 16px 9px 16px 42px;
    border: 2px solid #282828;
    &:before {
      background: #020202;
    }

    &:focus,
    &:active {
      /*box-shadow: 0 4px 10px rgba(28, 28, 28, 0.6);*/
    }
    .button-icon {
      background: #020202;
    }
  }

  &-icon {
    position: absolute;
    top: 50%;
    left: 7px;
    transform: translateY(-50%);
    width: 36px;
    height: 36px;
    border-radius: 50%;
    background-color: #a87401;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 5;
  }

  &-text {
    color: #fff;
    font-size: 14px;
    line-height: 1;
    letter-spacing: 0.15em;
    text-transform: uppercase;
    font-weight: 700;
    position: relative;
    z-index: 5;
  }
}

/*****************************************************************************/
/* carousel slider */
/*****************************************************************************/
.why-slider,
.cashback-slider {
  .swiper {
    &-container {
      overflow-x: hidden;
      overflow-y: visible;
      overflow: unset !important;

      > .swiper-pagination {
        &-bullets {
          bottom: -60px !important;
          text-align: left;
        }
      }
    }
    &-pagination {
      @media (max-width: 600px) {
        display: none !important;
      }

      &-bullet {
        margin: 0 12px 0 0 !important;
        width: 20px !important;
        height: 30px !important;
        background: transparent !important;
        border-radius: unset !important;
        opacity: 1 !important;
        position: relative;

        &:hover {
          &:before {
            opacity: 0.7;
          }
        }

        &:before {
          content: "";
          position: absolute;
          top: 50%;
          left: 0;
          transform: translateY(-50%);
          width: 100%;
          height: 2px;
          background: #cecdcd;
          transition: opacity 0.3s ease;
        }

        &-active {
          pointer-events: none;
          &:before {
            background: #e0bc6c;
          }
        }
      }
    }

    &-wrapper {
      @media (max-width: 600px) {
        flex-direction: column;
      }
    }
  }
}
</style>



